.input-box-con > div > p.active {
    opacity: 1;
    right: 0;
    transition: all .3s;
}

.err-modal {
    transform: translateY(-15em);
    transition: all 1s;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

}

.err-modal.active {
    transform: translateY(0);

}

.succ-modal-card {
    transform: scale(0);
    opacity: 0;
    transform-origin: bottom;
    transition: all .6s;
    overflow: hidden;
    min-height: 12em;
    padding-top: 1em;
}

.succ-modal-card .check {
    transform: translateY(-4em);
    transition: all .6s;
    transition-delay: 1.4s;
}

.succ-modal-card.active .check {
    transform: translateY(0);
}

.succ-modal-card.active {
    transform: scale(1);
    opacity: 1;
}

.succ-modal-card::before {
    content: '';
    position: absolute;
    width: 80%;
    height: .13em;
    background-color: #222;
    transform: scaleX(0);
}

.succ-modal-card.active::before {
    
    animation: lineAnim 1.4s .6s forwards;

}


.succ-modal-card .text-msg {
    font-size: 1.2em;
}

@keyframes lineAnim {
    0% {
        transform: scaleX(0);
    } 

    50%,55% {
        transform: scaleX(100%);
    }

    100% {
        transform: scaleX(0);
    }
}

.succ-modal-card > div {
    height: 5em;
    /* border: 1px solid red; */
    width: 80%;

    display: flex;
    justify-content: center;
    overflow: hidden;
}

.succ-modal-card > div > * {
    transition: all .6s;
    transition-delay: 1s;
} 

.succ-modal-card > div:nth-child(1) > * {
    transform: translateY(100%);
}

.succ-modal-card > div:nth-child(2) > * {
    transform: translateY(-100%);
}

.succ-modal-card.active > div:nth-child(1) > * {
    transform: translateY(-.5em);
}
.succ-modal-card.active > div:nth-child(2) > * {
    transform: translateY(.5em);
}

.succ-modal-card > div:nth-child(1) {
    align-items: end;   
}

.succ-modal-card > div:nth-child(2) {
    align-items: start;
}

.otp-inputs-con > input {

    border: 2px solid var(--brown);
    border-radius: .3em;
    outline: none;
    text-align: center;
    width: 100%;
    font-size: 1em;
    padding: .5em;

    transform: translateY(-2em);
    opacity: 0;
    transition: all .6s;
} 

.otp-inputs-con.active > input.error {
    border: 2px solid red;
    transition-delay: 0 !important;
}


.otp-inputs-con.active > input {
    opacity: 1;
    transform: translateY(0);
}

.otp-inputs-con.active > input:nth-child(1):not(.error) {
    transition-delay: .7s;
}

.otp-inputs-con.active > input:nth-child(2):not(.error) {
    transition-delay: .9s;
}

.otp-inputs-con.active > input:nth-child(3):not(.error) {
    transition-delay: 1.2s;
}

.otp-inputs-con.active > input:nth-child(4):not(.error) {
    transition-delay: 1.5s;
}

.otp-inputs-con.active > input:nth-child(5):not(.error) {
    transition-delay: 1.8s;
}

.email-btn-con button,
.email-btn-con p {
    transform: translateY(-2em);
    opacity: 0;
}

.email-btn-con.active button,
.email-btn-con.active p {
    transform: translateY(0);
    opacity: 1;
    transition: all .5s;

}

.email-btn-con.active button {
    transition-delay: 1.9s;
}

.email-btn-con.active p {
    transition-delay: 2s;
}


@media (min-width: 420px) {
    .err-modal {
        width: 22em;
    }

    body .succ-modal-card {
        width: 23em;
        min-height: 13em;
        
    }

    body .succ-modal-card .text-msg {
        font-size: 1.5em;
    }
    body .succ-modal-card button {
        font-size: 1.1em;
    }
}